import React from "react";
import { useState } from "react";
import {
  Container,
  Wrapper,
  Title,
  Desc,
  CardContainer,
  ToggleButtonGroup,
  ToggleButton,
  Divider,
} from "./ProjectsStyle";
import ProjectCard from "../Cards/ProjectCards";
import {
  GraphicDesignImages,
  cgi,
  photography,
  projects,
  videography,
} from "../../data/constants";
import videoEditingbg from "../../background/videoediting.jpg";
import graphicsDesignbg from "../../background/graphicsDesign.jpg";
import videographybg from "../../background/videography.jpg";
import photographybg from "../../background/photography.jpg";
import cgibg from "../../background/cgi.jpg";

import GalleryDesign from "./GalleryDesign";
import VideoEditing from "./VideoEditing";

const Projects = ({ openModal, setOpenModal }) => {
  return (
    <Container id="projects">
      <Wrapper>
        <Title>Projects</Title>
        <Desc>
          I have worked on a wide range of projects. From web apps to android
          apps. Here are some of my projects.
        </Desc>

        <CardContainer>
          <VideoEditing background={videoEditingbg} />
          <GalleryDesign
            title={"Graphic Designs"}
            media={GraphicDesignImages}
            background={graphicsDesignbg}
          />
          <GalleryDesign
            title={"VideoGraphy"}
            media={videography}
            background={videographybg}
          />
          <GalleryDesign
            title={"Photography"}
            media={photography}
            background={photographybg}
          />
          <GalleryDesign title={"CGI"} media={cgi} background={cgibg} />
        </CardContainer>
      </Wrapper>
    </Container>
  );
};

export default Projects;
