import React from "react";
import ReactDOM from "react-dom";
import styled from "styled-components";
import { advanced, instagram, motion } from "../../data/constants";
import Carousel from "./Carousel";

const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(1, 2, 44, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
`;

const ModalContent = styled.div`
  background: #222a35;
  padding: 20px;
  border-radius: 10px;
  width: 90%;
  max-width: 800px;
  max-height: 90%;
  overflow-y: auto;
  position: relative;
`;

const CloseButton = styled.button`
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
`;

const CardContainer = styled.div`
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
`;

const Card = styled.div`
  padding: 20px;
  margin: 10px;
  width: 80%;
  aspect-ratio: 2;

  border-radius: 10px;
  background-color: white;
  text-align: center;
  cursor: pointer;
  &:hover {
    background-color: #c0c0c0;
  }
`;
const CardTitle = styled.span`
  display: block;
  margin-top: 1px;
  font-size: 15px;
  font-weight: bold;
  color: #333;
`;
const modalRoot = document.getElementById("modal-root");

const Modal = ({ onClose }) => {
  const handleClickOutside = (event) => {
    if (event.target === event.currentTarget) {
      onClose();
    }
  };

  return ReactDOM.createPortal(
    <ModalOverlay onClick={handleClickOutside}>
      <ModalContent onClick={(e) => e.stopPropagation()}>
        <CloseButton onClick={onClose}>&times;</CloseButton>
        <CardContainer>
          <Card>
            <CardTitle>Instagram Reels</CardTitle>
            <Carousel data={instagram} />
          </Card>
          <Card>
            <CardTitle>Advanced Reels</CardTitle>
            <Carousel data={advanced} />
          </Card>
          <Card>
            <CardTitle>Motion Graphics</CardTitle>
            <Carousel data={motion} />
          </Card>
        </CardContainer>
      </ModalContent>
    </ModalOverlay>,
    modalRoot
  );
};

export default Modal;
