import React, { useState, lazy, Suspense } from "react";
import styled from "styled-components";
import LazyLoad from "react-lazyload"; // Import LazyLoad

const MediaAlbumContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const AlbumTitle = styled.h1`
  margin-bottom: 20px;
`;

const MediaGrid = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;

const Thumbnail = styled.img`
  width: 150px;
  height: 150px;
  margin: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;

  &:hover {
    transform: scale(1.1);
  }
`;

const LightboxOverlay = styled.div`
  border: 5px solid red;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
`;

const LightboxContainer = styled.div`
  border: 5px solid yellow;
  background: white;
  padding: 20px;
  border-radius: 10px;
  width: 800px;
  height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
`;

const CloseButton = styled.button`
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
`;

const MediaWrapper = styled.div`
  border: 5px solid green;
  max-width: 100%;
  max-height: 100%;
  min-width: 90%;
  min-height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.5s ease-in-out;
`;

const NavButton = styled.button`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  z-index: 1001;
`;

const PrevButton = styled(NavButton)`
  left: 0;
`;

const NextButton = styled(NavButton)`
  right: 0;
`;

const MediaAlbum = ({ media, title }) => {
  const [selectedMedia, setSelectedMedia] = useState(null);

  const openLightbox = (index) => {
    setSelectedMedia(index);
  };

  const closeLightbox = () => {
    setSelectedMedia(null);
  };

  const handlePrev = () => {
    const newIndex = (selectedMedia - 1 + media.length) % media.length;
    setSelectedMedia(newIndex);
  };

  const handleNext = () => {
    const newIndex = (selectedMedia + 1) % media.length;
    setSelectedMedia(newIndex);
  };

  return (
    <MediaAlbumContainer>
      <AlbumTitle>{title}</AlbumTitle>
      <MediaGrid>
        {media.map((item, index) => (
          <LazyLoad key={index} height={150}>
            <Thumbnail
              src={item.thumbnailLink || item.src}
              alt={item.name || `Media ${index + 1}`}
              onClick={() => openLightbox(index)}
            />
          </LazyLoad>
        ))}
      </MediaGrid>
      {selectedMedia !== null && (
        <Suspense fallback={<div>Loading...</div>}>
          <Lightbox
            media={media}
            selectedIndex={selectedMedia}
            onClose={closeLightbox}
            onPrev={handlePrev}
            onNext={handleNext}
          />
        </Suspense>
      )}
    </MediaAlbumContainer>
  );
};

const Lightbox = ({ media, selectedIndex, onClose, onPrev, onNext }) => {
  const currentMedia = media[selectedIndex];

  return (
    <LightboxOverlay>
      <LightboxContainer>
        <CloseButton onClick={onClose}>&times;</CloseButton>
        <MediaWrapper>
          {currentMedia.type === "image" ? (
            <img
              src={currentMedia.thumbnailLink}
              alt={currentMedia.name || `Media ${selectedIndex + 1}`}
              style={{ maxWidth: "100%", maxHeight: "100%" }}
            />
          ) : (
            <iframe
              src={currentMedia.url}
              title={currentMedia.name || `Media ${selectedIndex + 1}`}
              style={{
                width: "100%",
                height: "100%",
                border: "10px solid red",
              }}
              allowFullScreen
            />
          )}
        </MediaWrapper>
        <PrevButton onClick={onPrev}>&lt; Prev</PrevButton>
        <NextButton onClick={onNext}>Next &gt;</NextButton>
      </LightboxContainer>
    </LightboxOverlay>
  );
};

export default MediaAlbum;
