import React, { useState } from "react";
import styled from "styled-components";
import GalleryModal from "./GalleryModal"; // Assuming you have a GalleryModal component
import { GraphicDesignImages } from "../../data/constants";

const GraphicDesignTile = styled.div`
  width: 100%;
  aspect-ratio: 1.5;
  padding: 50px;
  margin: 20px 0;
  border-radius: 20px;
  background-image: url(${(props) => props.background});

  background-size: cover;
  cursor: pointer;
  text-align: center;
  align-content: center;
  color: #ffffff;
  font-size: 34px;
  text-shadow: 2px 2px #000000;
  text-decoration: overline;
  text-decoration-color: bisque;
  box-shadow: 1px 2px white;
  &:hover {
    background-color: #e0e0e0;
    transform: scale(1.01);
  }
`;

const Container = styled.div`
  box-shadow: 2px;
`;

const GalleryDesign = ({ title, media, background }) => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleClick = () => {
    setIsModalOpen(true);
  };

  const handleClose = () => {
    setIsModalOpen(false);
  };

  return (
    <Container>
      <GraphicDesignTile onClick={handleClick} background={background}>
        {title}
      </GraphicDesignTile>
      {isModalOpen && (
        <GalleryModal onClose={handleClose} media={media} title={title} />
      )}
    </Container>
  );
};

export default GalleryDesign;
